* {
  margin: 0;
}

body {
  background-color: black;
}

/* NavBar.js */

.navi {
  background-color: rgb(0, 0, 0);
}

.top {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.title {
  font-family: Snell Roundhand, cursive;
  color: #ffffff;
	font-size: calc((2.5 - 1) * 2vw + 1rem);
}

.top a {
  text-decoration: none;
  color: #ffffff;
}

.socials img{
	justify-content: space-between;
  max-width: calc((3 - 1) * 1vw + .5rem);
	text-decoration: none;
	color: #000000;
}

.logo {
  max-width: 10em;
  font-size: calc((2 - 1) * 1vw + .5rem);
}

.socials {
  color: #ffffff;
  text-decoration: none;
}

.socials a {
  margin: 1em;
  padding: .5em;
  text-decoration: none;
  color: #ffffff;
}

.menu {
  display: none;
}

.nav-bar{
	display: flex;
  background: #000000;
  border-top: 1px solid white;
  border-bottom: 1px solid white;
	justify-content: space-evenly;	
}

.nav-bar a{
	color: white;
	text-decoration: none;
  padding: .5em 0px;
}

.nav-bar a:hover{
  text-shadow:  1px 1px 3px red, 
                3px 3px 3px white; 
}

@media only screen and (max-width: 600px){

  .navi {
    border-bottom: 1px solid white;
  }

  .socials{
    display: none;
  }

  .nav-bar {
    display: none;
  }

  .top {
    border-bottom: 1px solid white;
  }

  .title {
    margin-left: -.75em;
  }

  .logo {
    margin-left: -2em;
  }

  .menu {
    display: flex;
    padding: 1em;
  }

  i {
    color: red;
    text-shadow:  0px 0px 1px white;
  }

  .mainRunner {
    margin-top: 1em;
  }

  .mainRunner img {
    max-width: 20em !important;
  }

  /* Ticker */

  .ticker {
    color: #ffffff;
    font-size: .8em;
    align-items: center;
    padding-top: .1em;
    padding-bottom: .1em;
  }

  .sym {
    padding: .1em;
    font-size: 1em;
    position: absolute;
    background-color: red;
    z-index: 1;
  }

  .update {
    display: flex;
    height: 1.3em;
  }

  .update p {
    position: absolute;
    width: 600px;
    white-space: nowrap;
    animation: moving 25s linear infinite;
  }

  @keyframes moving {
    0% {
      transform: translateX(100%);
    }

    100% {
      transform: translateX(-175%);
    }
  }

}

/* About.js */

.mainRunner {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #000000;
  flex-wrap: wrap;

}

.mainRunner img {
	height: auto; 
  width: auto; 
  max-width: 51.5%; 
  max-height: 600px;
	margin-right: 100px;
	margin-left: 100px;
}

#Bio {
	color: white;
	text-align: center;
	font-size: calc((1.65 - 1) * 2vw + .25rem);
	/* Found out that this scale the font size with screen size. The vw makes it scale */
}